import React, { useContext, useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import { GrobalContex } from "../../contexts/GlobalContex";
import { EducationInformationContex } from "./EducationInformationContex";

import LinearProgress from "@material-ui/core/LinearProgress";
//cookes manager

import cookie from "js-cookie";
import axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
    },
  },
}));

/// upload form system ///
export default function Uploadform() {
  const classes = useStyles();
  const [GlobalContexValue, setGlobalContexValue] = useContext(GrobalContex);
  const {
    Institute,
    Course,
    Subject,
    InstituteTrigger,
    CourseTrigger,
  } = useContext(EducationInformationContex);

  const [InstituteInfo, setInstituInfo] = Institute;
  const [CourseInfo, setCourseInfo] = Course;
  const [SubjectInfo, setSubjectInfo] = Subject;
  const [SelectInstituteTrigger, setSelectInstituteTrigger] = InstituteTrigger;
  const [SelectCourseTrigger, setSelectCourseTrigger] = CourseTrigger;

  const [Progressbar, setProgressbar] = useState(false);
  const [Issetfile, setIssetfile] = useState({ fileState: false, name: "" });
  const [Values, setValues] = useState({
    _institutes_id: "",
    _departments_id: "",
    _subjects_id: "",
    topic_name: "",
    level: "",
    field: "",
    singlenote: "",
  });

  const handleChange = (prop) => (event) => {
    console.log("handleChange");
    setValues({ ...Values, [prop]: event.target.value });
    console.log(JSON.stringify(Values));
  };

  const handelInstituteChange = (prop) => (event) => {
    let tergetObject1 = InstituteInfo.find(
      (obj) => obj._id === event.target.value
    );
    console.log(
      "handelInstituteChange >> tergetObject1 =" + JSON.stringify(tergetObject1)
    );
    // setValues({ ...Values, [prop]: tergetObject1.name });
    setValues({ ...Values, [prop]: event.target.value });
    setSelectInstituteTrigger(tergetObject1);
    console.log(JSON.stringify(Values));
  };

  const handelDepartmentChange = (prop) => (event) => {
    console.log("handelDepartmentChange event.target.value >> " + event.target.value);
      let tergetObject = CourseInfo.find(
      (obj) => obj._departments_id._id === event.target.value
    );
    console.log(
      "handelDepartmentChange >> tergetObject = " + JSON.stringify(tergetObject)
    );
    // setValues({ ...Values, [prop]: tergetObject._department_id.name });
    setValues({ ...Values, [prop]: event.target.value });
    setSelectCourseTrigger(tergetObject);

    console.log(JSON.stringify(Values));
  };

  const handleChangeFile = (prop) => (event) => {
    setValues({ ...Values, [prop]: event.target.files[0] });

    console.log(" | 57 Debug event.target.files | " + event.target.files[0]);
    console.log(JSON.stringify(Values));
    console.log(JSON.stringify(event.target.files[0].name));
    console.log(JSON.stringify(Values.singlenote));

    setIssetfile({
      ...Issetfile,
      fileState: true,
      name: JSON.stringify(event.target.files[0].name),
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setProgressbar(true);

    console.log("handleSubmit");

    const formData = new FormData();

    //     _institutes_id : req.body._institutes_id,
    //     _departments_id: req.body._departments_id,
    //     _subjects_id: req.body._subjects_id,
    //     topic_name: req.body.topic_name,
    //     level: req.body.level,
    //     field: req.body.field,

    formData.append("enctype", "multipart/form-data");

    formData.append("_institutes_id", Values._institutes_id);
    formData.append("_departments_id", Values._departments_id);
    formData.append("_subjects_id", Values._subjects_id);
    formData.append("topic_name", Values.topic_name);
    formData.append("level", Values.level);
    formData.append("field", Values.field);
    formData.append("singlenote", Values.singlenote);

    console.log(formData);

    axios({
      // `url` is the server URL that will be used for the request
      url: "/api/notes/users-upload-single-notes",

      // `method` is the request method to be used when making the request
      method: "post",

      // `baseURL` will be prepended to `url` unless `url` is absolute.
      baseURL: GlobalContexValue.url,

      // `headers` are custom headers to be sent
      headers: {
        "X-Requested-With": "XMLHttpRequest",
        Authorization: "Bearer" + " " + cookie.get("access"),
      },

      // `data` is the data to be sent as the request body
      data: formData,
    })
      .then((result) => {
        console.log(result);
        setProgressbar(false);
        alert("Your Notes Is Succefull Uploaed");
        window.location.reload();
      })
      .catch((error) => {
        console.log(error);
        setProgressbar(false);
        alert("Your Notes Is NOT Uploaed");

        // window.location.reload();
      });
  };

  //  desplay part   ///
  return (
    <form
      className={classes.root}
      noValidate
      autoComplete="off"
      onSubmit={handleSubmit}
    >
      <TextField
        id="outlined-select-currency-native"
        select
        required
        label="institute"
        name="_institutes_id"
        helperText="Select a Institute"
        onChange={handelInstituteChange("_institutes_id")}
        style={{ width: "230px" }}
        SelectProps={{
          native: true,
        }}
        variant="outlined"
      >
        <option value={""}></option>
        {Object.values(InstituteInfo).map((Option) => (
          <option key={Option._id} value={Option._id}>
            {Option.name}
          </option>
        ))}
      </TextField>

      <TextField
        id="outlined-select-currency-native"
        select
        required
        label="department"
        name="_departments_id"
        variant="outlined"
        onChange={handelDepartmentChange("_departments_id")}
        style={{ width: "230px" }}
        SelectProps={{
          native: true,
        }}
      >
        <option value=""></option>
        {Object.values(CourseInfo).map((Option) => (
          <option key={Option._id} value={Option._departments_id._id}>
            {Option._departments_id.name}
          </option>
        ))}
      </TextField>

      <TextField
        id="outlined-select-currency-native"
        select
        required
        label="Subject Name"
        name="_subjects_id"
        variant="outlined"
        onChange={handleChange("_subjects_id")}
        style={{ width: "230px" }}
        SelectProps={{
          native: true,
        }}
      >
        <option value=""></option>
        {Object.values(SubjectInfo).map((Option) => (
          <option key={Option._id} value={Option._id}>
            {Option.name}
          </option>
        ))}
      </TextField>

      <TextField
        required
        id="outlined-basic"
        label="topic name"
        variant="outlined"
        name="topic_name"
        onChange={handleChange("topic_name")}
      />
      <TextField
        required
        id="outlined-basic"
        label="Level"
        variant="outlined"
        name="level"
        onChange={handleChange("level")}
      />
      <TextField
        required
        id="outlined-basic"
        label="field"
        variant="outlined"
        name="field"
        onChange={handleChange("field")}
      />
      <div className={classes.root}>
        <span> {Issetfile.fileState ? "File : " + Issetfile.name : ""}</span>
      </div>

      <div className={classes.root}>
        {Progressbar ? <LinearProgress color="secondary" /> : ""}
      </div>

      <input
        // multiple
        required
        accept="/*"
        className={classes.input}
        id="contained-button-file"
        name="singlenote"
        type="file"
        style={{ display: "none" }}
        onChange={handleChangeFile("singlenote")}
      />

      <label htmlFor="contained-button-file">
        <Button
          variant="contained"
          color="inherit"
          component="span"
          startIcon={<CloudUploadIcon />}
          size="large"
        >
          select file to upload
        </Button>
      </label>

      <Button variant="contained" color="secondary" size="large" type="submit">
        Submmit
      </Button>

      {/* {JSON.stringify(InstituteInfo)} */}
      {/* {JSON.stringify(CourseInfo)} */}

      {/* {Object.values(InstituteInfo).map((option) => {
        return <p> {option.name} {option.id}</p>;
      })} */}
      
    </form>
  );
}
