import React from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

import cookie from "js-cookie";
import Appbar from "./components/appbar";
import LogInAppBar from "./components/LogInAppBar";

import Home from "./pages/home/Main";
import Notes from "./pages/notes/Main";
import Institute from "./pages/institute/Main";
import Department from "./pages/department/Main";
import Subject from "./pages/subject/Main";

import Login from "./pages/login/Main";
import Signup from "./pages/signup/Main";
import Profile from "./pages/profile/Main";
import Upload from "./pages/upload/Main";
import Logout from "./pages/logout/Main";

function template() {
  const isLoggedIn = cookie.get("isLoggedIn");

  if (!isLoggedIn) {
    return (
      <div>
        <Router>
          <LogInAppBar />
          <Switch>
            <Route path="/" exact component={Login} />
            <Route path="/home" exact component={Home} />
            <Route path="/Notes/:notesid" exact component={Notes} />
            <Route path="/institute/:instituteid" exact component={Institute} />
            <Route path="/department/:departmentid" exact component={Department}/>
            <Route path="/subject/:subjectid" exact component={Subject} />
            <Route path="/login" exact component={Login} />
            <Route path="/Signup" exact component={Signup} />
            <Route path="/*" component={Login}/>
          </Switch>
        </Router>
      </div>
    );
  }

  return (
    <div>
      <Router>
        <Appbar />
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/home" exact component={Home} />
          <Route path="/institute/:instituteid" exact component={Institute} />
          <Route path="/department/:departmentid" exact component={Department}/>
          <Route path="/subject/:subjectid" exact component={Subject} />
          <Route path="/Notes/:notesid" exact component={Notes} />
          <Route path="/Profile" exact component={Profile} />
          <Route path="/upload" exact component={Upload} />
          <Route path="/Logout" exact component={Logout} />
          <Route path="/*" exact component={Home} />
        </Switch>
      </Router>
    </div>
  );
}

export default template;
