import theme from "./theme/theme";


function App() {
  return (

    <ThemeProvider theme={theme}>
      <GlobalContexProvider>
        <div className="App">
          <Template />
        </div>
      </GlobalContexProvider>
    </ThemeProvider>
  );
}

export default App;
