import React, { useState, useEffect, useContext } from "react";
import { GrobalContex } from "../../contexts/GlobalContex";
import axios from "axios";
// cookie managers
import cookie from "js-cookie";

import { Link } from "react-router-dom";

import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";


import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";




function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright © "}
      <Link color="inherit" href="https://shattak.com/">
        Shattak
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function Main() {
  const classes = useStyles();

  //BLOCK Contex API
  const [GlobalContexValue, setGlobalContexValue] = useContext(GrobalContex);

  //BLOCK State management
  const [values, setValues] = useState({
    email: "",
    password: "",
  });

  const [logintrigger, setlogintrigger] = useState(false);
  const [reLoadOnce, setreLoadOnce] = useState(false)

  //  handel functions
  const handleChange = (props) => (event) => {
    setValues({ ...values, [props]: event.target.value });
    console.log(values);
  };

  const handleFormSubmit = (event) => {
    event.preventDefault();
    setlogintrigger(!logintrigger)
  };

  //BLOCK Effects Hokes
  useEffect(() => {
    console.log(GlobalContexValue.url);
    axios({
      // `url` is the server URL that will be used for the request
      url: "/api/authentications/request-users-login",

      // `method` is the request method to be used when making the request
      method: "post", 

      // `baseURL` will be prepended to `url` unless `url` is absolute.
      baseURL: GlobalContexValue.url,

      // `headers` are custom headers to be sent
      headers: { "X-Requested-With": "XMLHttpRequest" },

      // `data` is the data to be sent as the request body
      data: {
        email:values.email,
        password:values.password,
      },
    })
      .then((result) => {
        console.log(JSON.stringify(result.data));
         // set cookes

        if(result.data)

         cookie.set("access", result.data.access, { expires: 30  });
         cookie.set("refresh", result.data.refresh, { expires: 30 });

         cookie.set("_users_id", result.data._users_id, { expires: 30 });
         cookie.set("users_name", result.data.users_name, { expires: 30 });
 
         cookie.set("isLoggedIn", true, { expires: 30 });
         window.location.reload();
         window.location.assign("/home")

      })
      .catch((errors) => {
        console.log(JSON.stringify(errors));
      });

    return () => {};
  }, [logintrigger,reLoadOnce]);

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Log in
        </Typography>

        <form className={classes.form} noValidate onSubmit={handleFormSubmit}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            value={values.email}
            onChange={handleChange("email")}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            value={values.password}
            autoComplete="current-password"
            onChange={handleChange("password")}
          />
          {/* <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          /> */}
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Log In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link to="/signup">{"Don't have an account? Sign Up"}</Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
  );
}
