import React, { useEffect, useContext, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import MediaControlCard from "../../components/MediaControlCard";
import { GrobalContex } from "../../contexts/GlobalContex";

import axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

export default function Structure() {
  const classes = useStyles();
  const [GlobalContexValue, setGlobalContexValue] = useContext(GrobalContex);

  const [Notes, setNotes] = useState();

  useEffect(() => {
    axios({
      url: "api/notes/fetch-all-notes-info",

      method: "get",

      baseURL: GlobalContexValue.url,

      headers: { "X-Requested-With": "XMLHttpRequest" },

      data: {},
    })
      .then((result) => {
        console.log(JSON.stringify(result.data.result));
        setNotes(result.data.result);
        console.log(JSON.stringify(Notes));
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <div className={classes.root}>
      {/* {JSON.stringify(Notes)} */}

      <Grid container spacing={1}>
        {
        Notes && Notes.map((notesItem) => {
          return (
            <Grid key={notesItem._id} item xs={6} sm={3}>
              <MediaControlCard  {...notesItem} />
            </Grid>
          );
        })
        }

        {/* <Grid item xs={6} sm={3}>
          <MediaControlCard />
        </Grid> */}
 
      </Grid>
    </div>
  );
}
