import React, { useEffect, useContext, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import MediaControlCard from "../../components/MediaControlCard";
import { GrobalContex } from "../../contexts/GlobalContex";
import Cards from "../../components/Cards"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch,
} from "react-router-dom";

import Button from "@material-ui/core/Button";

import axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

export default function NotesList() {
  const classes = useStyles();
  const [GlobalContexValue, setGlobalContexValue] = useContext(GrobalContex);

  const [Notes, setNotes] = useState();
  const [Subject, setSubject] = useState();
  let { subjectid } = useParams();
  useEffect(() => {
    axios({
      url: "api/notes/fetch-notes-by-id",

      method: "post",

      baseURL: GlobalContexValue.url,

      headers: { "X-Requested-With": "XMLHttpRequest" },

      data: { _subjects_id: subjectid },
    })
      .then((result) => {
        console.log(JSON.stringify(result.data.result));
        setSubject(result.data.result);
        console.log(JSON.stringify(Subject));
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <div className={classes.root}>
      {/* {JSON.stringify(Deperment) } */}

      <Grid container spacing={1}>
        {Subject &&
          Subject.map((SubjectItems) => {
            return (
              <Grid key={SubjectItems._id} item xs={12} sm={3}>
                <Cards {...SubjectItems} />
          
              </Grid>
            );
          })}
      </Grid>
    </div>
  );
}
