import React, { useContext, useEffect, useState } from "react";
import { EducationInformationContex } from "./EducationInformationContex";
import axios from "axios";
import { GrobalContex } from "../../contexts/GlobalContex";

export default function Educationalstructureinfo() {
  const [GlobalContexValue, setGlobalContexValue] = useContext(GrobalContex);
  const {
    Institute,
    Course,
    Subject,
    InstituteTrigger,
    CourseTrigger,
  } = useContext(EducationInformationContex);

  const [InstituteInfo, setInstituInfo] = Institute;
  const [CourseInfo, setCourseInfo] = Course;
  const [SubjectInfo, setSubjectInfo] = Subject;
  const [SelectInstituteTrigger, setSelectInstituteTrigger] = InstituteTrigger;
  const [SelectCourseTrigger, setSelectCourseTrigger] = CourseTrigger;

  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+

  useEffect(() => {
    // InstituteInfo

    console.log("Educationalstructureinfo ======== InstituteInfo");
    axios({
      url: "management/v1/dashboard/fetch-institute-info-with-id",

      method: "post",

      baseURL: GlobalContexValue.url,

      headers: { "X-Requested-With": "XMLHttpRequest" },

      data: {},
    })
      .then((result) => {
        console.log(
          " result AXIOS ============ " + JSON.stringify(result.data.result)
        );
        setInstituInfo(result.data.result);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+

  useEffect(() => {
    try {
      axios({
        url: "management/v1/dashboard/fetch-course-info-with-id/",

        method: "post",

        baseURL: GlobalContexValue.url,

        headers: {
          "X-Requested-With": "XMLHttpRequest",
          "Content-Type": "application/json",
        },

        data: { _institutes_id: SelectInstituteTrigger._id },
      })
        .then((result) => {
          console.log(
            "result.data.result (CourseInfo) >>>" +
              JSON.stringify(result.data.result)
          );
          setCourseInfo(result.data.result);
          console.log(CourseInfo);
        })
        .catch((error) => {
          console.log(error);
        });
    } catch (error) {
      console.log(error);
    }
  }, [SelectInstituteTrigger]);

  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+
  ///+-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-++-+-+-+-+

  useEffect(() => {
    //SubjectInfo
    console.log(
      "Educational structure INFO  >>> SelectDepartmentTrigger >>>> " +
        JSON.stringify(SelectCourseTrigger)
    );

    // {"_id":"5fa43ccb94f8f85369136a08","_institute_id":"5fa425fd416d1537a904b7a4","_departments_id":{"_id":"5fa41f772ba58e2d8cb0faaa","name":"camestry"},"__v":0}
    try {
      axios({
        url: "management/v1/dashboard/fetch-subjects-info-with-id",

        method: "post",

        baseURL: GlobalContexValue.url,

        headers: { "X-Requested-With": "XMLHttpRequest" },

        data: {
          _departments_id: SelectCourseTrigger._departments_id._id,
        },
      })
        .then((result) => {
          console.log(JSON.stringify(result.data.result));
          setSubjectInfo(result.data.result);
        })
        .catch((error) => {
          console.log(error);
        });
    } catch (error) {
      console.log(error);
    }
  }, [SelectCourseTrigger]);

  return (
    <div>
      {/* 
      {JSON.stringify(InstituteInfo)}
      <br></br>
      <br></br>

      {JSON.stringify(CourseInfo)}
      <br></br>
      <br></br> */}

      {/* {JSON.stringify(SubjectInfo)} */}
    </div>
  );
}


