import React, { useEffect, useContext, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import MediaControlCard from "../../components/MediaControlCard";
import { GrobalContex } from "../../contexts/GlobalContex";
import { Link } from "react-router-dom";

import Button from "@material-ui/core/Button";

import axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

export default function InstituteList() {
  const classes = useStyles();
  const [GlobalContexValue, setGlobalContexValue] = useContext(GrobalContex);

  const [Notes, setNotes] = useState();
  const [Institute, setInstitute] = useState();

  useEffect(() => {
    axios({
      url: "management/v1/dashboard/fetch-institute-info-with-id",

      method: "post",

      baseURL: GlobalContexValue.url,

      headers: { "X-Requested-With": "XMLHttpRequest" },

      data: {},
    })
      .then((result) => {
        console.log(JSON.stringify(result.data.result));
        setInstitute(result.data.result);
        console.log(JSON.stringify(Institute));
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <div className={classes.root}>
      {/* {JSON.stringify(Notes)} */}

      <Grid container spacing={1}>
        {Institute &&
          Institute.map((InstituteItems) => {
            return (
              <Grid key={InstituteItems._id} item xs={6} sm={3}>
                <Link
                  to={`/institute/${InstituteItems._id}`}
                  className={classes.navstyle}
                >
                  <Button variant="outlined" size="large">
                    {InstituteItems.name}
                  </Button>
                </Link>
              </Grid>
            );
          })}

        {/* <Grid item xs={6} sm={3}>
          <MediaControlCard />
        </Grid> */}
      </Grid>
    </div>
  );
}
