import React, { useState, createContext } from "react";
export const EducationInformationContex = createContext();

export const EducationInformationContexProvider = (props) => {
  const [InstituteInfo, setInstituInfo] = useState({});
  const [CourseInfo, setCourseInfo] = useState({});
  const [SubjectInfo, setSubjectInfo] = useState({});

  const [SelectInstituteTrigger, setSelectInstituteTrigger] = useState({"_departments_id":[],"_id":"","name":""});
  const [SelectCourseTrigger, setSelectCourseTrigger] = useState({"_id":"","_institutes_id":"","_departments_id":{"_id":"","name":""}});
 
  return (
    <EducationInformationContex.Provider
      value={{
        Institute: [InstituteInfo, setInstituInfo],
        Course: [CourseInfo, setCourseInfo],
        Subject: [SubjectInfo, setSubjectInfo],
        InstituteTrigger: [SelectInstituteTrigger, setSelectInstituteTrigger],
        CourseTrigger: [
          SelectCourseTrigger,
          setSelectCourseTrigger,
        ],
      }}
    >
      {props.children}
    </EducationInformationContex.Provider>
  );
};
